import * as React from "react";
import styled from "styled-components";

interface JopItemProps {
    style?: any;
}

const Content = styled.div`
    width: 100%;
    height: 200px;
    display: flex;
    align-items: center;
    background-color: white;
    border-bottom: 1px solid #e6e6e6;
    & > span {
        width: 20%;
        display: inline-block;
        padding: 0 20px;
        font-size: 15px;
        &:first-child {
            color: #1e1e1e;
        }
        &:nth-child(2) {
            width: 60%;
            color: #1e1e1e;
            & > a {
                color: #333;
                text-decoration: none;
                display: block;
                font-size: 17px;
                border-bottom: 2px;
            }
        }
        &:nth-child(3) {
            width: 40%;
            color: #1e1e1e;
            & > a {
                color: #333;
                text-decoration: none;
                display: block;
                font-size: 17px;
                border-bottom: 2px;
            }
        }

        &:last-child {
            width: 20%;
            border: none;
            color: #1e1e1e;
        }
    }
`;
const JopItem: React.SFC<JopItemProps> = props => {
    return (
        <Content style={props.style}>
            <span>유니언플레이스</span>
            <span>
                <a href="/#/career-detail/1">2019년 상반기 각부문 정규직 공개채용</a>
                <span>신입·경력 | 대졸이상 | 정규직, 계약직</span>
            </span>
            <span>기획 / 디자인 / 설계 / 운영</span>
            <span>상시채용</span>
        </Content>
    );
};

export default JopItem;



